import React,{Component} from 'react';
import {getSearch} from '../../../fetch/search'
import ListComponent from "../../../components/ListComponent/index";
import LoadMore from "../../../components/LoadMore/index";
export default class SearchList extends Component{
  constructor(){
    super();
    this.state = {
      hasMore:true, //
      data:[],//
      page:0,//
      isLoading:true//
    }
  }
  render(){
    return (
      <div>
        {/*ListComponent */}
        {this.state.data.length?
          <ListComponent data={this.state.data}/>
          :<div></div>}
        <LoadMore hasMore={this.state.hasMore} loadMore={this.loadMore.bind(this)} isLoading={this.state.isLoading}/>
      </div>
    )
  }
  componentDidMount(){
    this.processData(getSearch(this.props.cityName,0));
  }
  //loadMore
  loadMore(){
    console.log('loading more.....');
    this.setState({
      page:this.state.page+1,
      isLoading:true//
    },()=>{ //
      this.processData(getSearch(this.props.cityName,this.state.page));
    });
  }

  //
  processData(result){
    result.then(res=>res.json()).then(({hasMore,data})=>{
      this.setState({
        hasMore,
        data:this.state.data.concat(data),
        isLoading:false
      });
    })
  }
}


